@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Mansalva&display=swap);
@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Display|Turret+Road|Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: grid;
  background-color:#185652;
}
p{
  color: white;
  font-size: 50px;
  font-family: 'Mansalva', cursive;
}
.nav{
  height: 100px;
}
.op{
  opacity: 0.2;
}
.linkItems{
  font-size: 30px;
  color: white;
  font-family: "Montserrat",sans-serif;
}
.linkItems > .active {
  /* border-bottom: 1px solid red; */
  background-color: #fec503;
  border-radius: 3px;
  padding:10px 15px;
  display: block;
}
.lorem1{
  font-size: 30px;
  color: white;
  font-family: 'Turret Road', cursive;
  text-align: justify;
  padding: 50px;
 
}
.lorem2{
  font-size: 30px;
  color: white;
  font-family: 'Big Shoulders Display', cursive;
  text-align: justify;
  padding: 50px;
  

}
.lorem3{
  font-size: 30px;
  color: white;
  font-family: 'Vibes', cursive;
  text-align: justify;
  padding: 50px;
  text-align: justify;
  

}
@media only screen and (max-width: 600px) {
  .App {
    background-color:#185652; 
  }
  .lorem1{
    font-size: 20px;
    padding: 20px;
    text-align: justify;
  }
  .lorem2{
    font-size: 20px;
    padding: 20px;
    text-align: justify;
  }
  .lorem3{
    font-size: 20px;
    padding: 20px;
    text-align: justify;
  }
  }





body{
    background: -webkit-linear-gradient(left, #0072ff, #00c6ff);
}
.contact-form{
    background: #fff;
    margin-top: 10%;
    margin-bottom: 5%;
    width: 70%;
}
.contact-form .form-control{
    border-radius:1rem;
}
.contact-image{
    text-align: center;
}
.contact-image img{
    border-radius: 6rem;
    width: 11%;
    margin-top: -3%;
    -webkit-transform: rotate(29deg);
            transform: rotate(29deg);
}
.contact-form form{
    padding: 14%;
}
.contact-form form .row{
    margin-bottom: -7%;
}
.contact-form h3{
    margin-bottom: 8%;
    margin-top: -10%;
    text-align: center;
    color: #0062cc;
}
.contact-form .btnContact {
    width: 50%;
    border: none;
    border-radius: 1rem;
    padding: 1.5%;
    background: #dc3545;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
}
.btnContactSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    color: #fff;
    background-color: #0062cc;
    border: none;
    cursor: pointer;
}
